.checkout-page{
    width: 55%;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px auto;

    .header{
        width: 100%;
        height: 40px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid darkgrey;

        .header-block{
            text-transform: capitalize;
            width: 23%;

            &:last-child{
                width: 8%;
            }
        }
    }

    .payment-bottom{
        display: flex;
        justify-content: flex-end;
        width: 100%;
        align-items: center;

        .total {
            margin-top: 30px;
            margin-left: auto;
            font-size: 36px;
        }

        button{
            margin-top: 30px;
            margin-left: 20px;
        }
    }
    .test-warning {
        text-align: center;
        margin-top: 40px;
        font-size: 24px;
        color: red;
      }
    
}