.collection-item{
    width: 22vw;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;

    .background-img{
        background-size: cover;
        background-position: center;
        width: 100%;
        height: 95%;
        margin-bottom: 5px;
    }

    button{
        width: 80%;
        opacity: 0.7;
        position: absolute;
        top: 255px;
        display: none;
    }

    &:hover{
        .background-img{
            opacity: 0.8;
        }
        button{
            opacity: 0.85;
            display: flex;
        }
    }
    .footer{
        width: 100%;
        height: 5%;
        display: flex;
        justify-content: space-between;
        font-size: 18px;

        .name{
            width: 90%;
            margin-bottom: 15px;
        }
    }
}